.body {
  font-family: sans-serif;
  text-align: center;
  background: black;
  color: white;
}
.ader {
  font-family: sans-serif;
}
.grid-main {
  display: grid;
  grid-template-columns: auto auto;
  gap: 10px;
  text-align: center;
}
.grid-child {
  border: 1px solid whitesmoke;
  border-radius: 15px;
  padding: 10px;
  cursor: pointer;
}
.grid-child:hover {
  background-color: black;
  color: white;
}
.grid-child h3 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 700;
}
.grid-child p {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 500;
  font-size: 12px;
}

.text-area {
  font-size: 20px;
  background: gray;
  color: white;
  padding: 10px;
}
.action-btn {
  width: 200px;
  height: 50px;
  font-size: 20px;
  margin-top: 20px;
  cursor: pointer;
}
.result-text {
  display: flex;
  height: 30%;
  width: 30%;
}
.text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
